
export interface ImapData {
    width: number;
    height: number;
    map: string;
    mapId: number;
    origin: { x: number; y: number };
    roomInfo: string;
    pathData: { x: number; y: number, type: string }[];
    pilePosition: {x: number, y: number};
    areaData: any[];
    mapConfig: IMapConfig;
}

export interface IMapConfig {
    robotColor: string;
    pileType: string;
    pileColor: string;
    floorType: FloorMaterial;
    pathWidth?: number;
    wallColor?: string;
    wallHeight?: number;
    roomColors?: string[];
    showVirtualArea?: boolean;
}

enum ObjectType {
    path  = 'map_path',
    wall  = 'map_wall',
    floor = 'map_floor',
    robot = 'robot',
    charge= 'charge',
    area  = 'area'
 }

enum FloorMaterial {
    zone = 'zone',
    wood = 'wood',
    tile = 'tile',
}

export {
    ObjectType,
    FloorMaterial
}