
export const isEmpty = (obj: any): boolean => {
    if (obj === null || obj === undefined || isNaN(obj)) {
        return true
    } else {
        return false
    }
}

export const calculateAngle = (pt1: {x: number, y: number}, pt2: {x: number, y: number}) : number => {
    let angle = Math.atan2(pt1.y - pt2.y, pt2.x - pt1.x);  //* 180 / Math.PI
    // if(angle < 0) return angle < 0 ? 360 + angle : angle
    return angle;
}

export const twoPointDistance = (pt1: {x: number, y: number}, pt2: {x: number, y: number}): number => {
    return Math.sqrt(Math.pow(pt2.y-pt1.y,2)+Math.pow(pt2.x-pt1.x,2))
}

/**
 * 
 * @param x
 * @param y
 */
export const deepEqual = function (x: any, y: any) {
    if (x === y) {
        return true;
    }
    if (typeof x === 'object' && x != null && typeof y === 'object' && y != null) {
        if (Object.keys(x).length !== Object.keys(y).length) return false;
        for (const prop in x) {
            if (y.hasOwnProperty(prop)) {
                if (!deepEqual(x[prop], y[prop])) return false;
            } else return false;
        }
        return true;
    }
    return false;
};

export const showLog = (log: string | any) => {
    let logStr = ''
    if (log === undefined || log === null) {
        logStr = log === undefined ? 'undefined' : 'null'
    } else {
        logStr = typeof(log) === 'string' ? log : typeof(log) === 'number' ? log.toString() : JSON.stringify(log)
    }
    window.postMessage(logStr)
}

export function memorize<T>(fn: Function, resolve?: (...params: any[]) => any) {
    const toStr = Object.prototype.toString;
    const isFunction = (fn: any) => {
      return typeof fn === 'function' && toStr.call(fn) === '[object Function]';
    };
    const isAsyncFunction = (fn: any) => {
      return typeof fn === 'function' && toStr.call(fn) === '[object AsyncFunction]';
    };
  
    const isValid = (value: any) => typeof value !== 'undefined';
  
    if (!isFunction(fn)) {
      throw new TypeError('memorize: when provided, the first argument must be a function');
    }
    const cache: { [index: string]: any } = {};
  
    function result(...args: any) {
      if (args.length < 1) {
        throw new TypeError('memorize: arguments cannot be null or undefined');
      }
      const key = resolve
        ? resolve(...args)
        : args.length === 1
          ? `${args[0]}`
          : JSON.stringify(args);
      cache[key] = (isValid(cache[key]) && cache[key]) || fn.apply(fn, args);
  
      return cache[key] as T;
    }
  
    if (isAsyncFunction(fn)) {
      return async function (...args: any) {
        return result(...args);
      };
    }
  
    return result;
  }