export interface boxProps {
    direction: string,
    length: number
}

const colorMap = [[105,194,237],[109,142,211],[101,208,180],[204,130,217],[140,177,121],[226,137,147],[234,157,97],[134,164,223],
    [136,183,168],[176,169,213],[188,213,151],[226,97,97],[223,191,112],[201,174,174],[174,184,154],[254,172,178],[242,101,100],[54,227,244],[54,64,217],
    [63,242,119],[149,250,97],[231,108,162],[158,104,99],[242,53,144],[194,237,160],[90,154,59],[191,143,71],[143,117,174],[120,234,52],[228,107,207]]

export const getWallData = (map: string, width: number, pilePst: {x: number, y: number}): {x: number, y: number, p: boxProps}[] => {
    var indexArr: number[] = []
    var boxArr: {x: number, y: number, p: boxProps}[] = []
    for (let i = 0; i < map.length / 2; i++) {
        let idx = indexArr.indexOf(i)
        if (idx > -1) {
            indexArr.splice(idx, 1)
            continue
        }
        let ptx = i % width
        let pty = Math.floor(i / width)
        //
        if (Math.abs(ptx - pilePst.x) < 2 && Math.abs(pty - pilePst.y) < 2) {
            continue
        }
        let ptv = parseInt(map.slice(i * 2, i * 2 + 2), 16)
        if(ptv === 1) {
            let boxP = getBoxWidth(map,width,i+1,indexArr,1)
            if (boxP.length === 1) {
                boxArr.push({ x: ptx, y: pty, p: boxP })
            } else if (boxP.direction === 'row'){
                boxArr.push({ x: ptx - 0.5 + boxP.length / 2, y: pty, p: boxP })
            } else {
                boxArr.push({ x: ptx, y: pty - 0.5 + boxP.length / 2, p: boxP })
            }
        }
    }

    return boxArr
}

const getBoxWidth = (map: string, width: number, index: number, indexArr: number[], boxW: number): boxProps => {
    let ptx = index % width
    if (ptx < width - 1 && !indexArr.includes(index)) {
        indexArr.push(index)
        let ptv = parseInt(map.slice(index * 2, index * 2 + 2), 16)
        if (ptv === 1) {
            return getBoxWidth(map,width,index+1,indexArr,boxW+1)
        } else if (boxW > 1){
            if (boxW === 2) { //2
                let boxH = getColumnLength(map, width, index - boxW + width, 1)
                if(boxH > boxW) {
                    indexArr.splice(indexArr.length - boxW, boxW)
                    return getBoxHeight(map, width, index - boxW + width, indexArr, 1)
                } else {
                    return {direction: 'row', length: boxW}
                }
            } else {
                return {direction: 'row', length: boxW}
            }
        } else {
            return getBoxHeight(map, width, index - 1 + width, indexArr, 1)
        }
    } else {

        if (boxW > 1){
            return {direction: 'row', length: boxW}
        } else {
            return getBoxHeight(map, width, index - 1 + width, indexArr, 1)
        }
    }
}

const getBoxHeight = (map: string, width: number, index: number, indexArr: number[], boxH: number): boxProps => {
    if (index < map.length / 2) {
        indexArr.push(index)
        let ptv = parseInt(map.slice(index * 2, index * 2 + 2), 16)
        if (ptv === 1) {
            return getBoxHeight(map,width,index+width,indexArr,boxH+1)
        } else {
            return {direction: 'column', length: boxH}
        }
    } else {
        return {direction: 'column', length: boxH}
    }
}

const getColumnLength = (map: string,width: number,index: number,length: number): number => {
    if (index < map.length / 2) {
        let ptv = parseInt(map.slice(index * 2, index * 2 + 2), 16)
        return ptv === 1 ? getColumnLength(map,width,index+width,length+1) : length
    } else {
        return length
    }
}

export const getZoneColor = (zoneId: number, haveZone: boolean = false, zoneColors?: string[]): number[] => {
    if((zoneId >> 2) > 2 && zoneId < colorMap.length) {
        return zoneColors ? hexToRgb(zoneColors[(zoneId >> 2) - 3]) : colorMap[(zoneId >> 2) - 3]
    } else if(zoneId === 0){
        return haveZone ? [170,170,170] : [100,150,250]
    } else {
        return [0,0,0]
    }
}

export const getChargeDirection = (mapData: ImapData): number => {

    let chargeX = mapData.pilePosition.x + mapData.origin.x
    let chargeY = mapData.pilePosition.y + mapData.origin.y

    if(chargeX > mapData.width / 2){
        return Math.PI
    } else {
        return 0
    }
}

const hexToRgb = (hex: string): number[] => {

    let hexStr = hex.replace("#", "");

    let r = parseInt(hexStr.substring(0, 2), 16);
    let g = parseInt(hexStr.substring(2, 4), 16);
    let b = parseInt(hexStr.substring(4, 6), 16);
 
    return [r,g,b];
}

interface Point {
    x: number,
    y: number
}