
import * as THREE from "three";
import {boxProps} from "./mapUtils";
import { ObjectType } from "../config/interface";
import { calculateAngle, twoPointDistance } from ".";
import { deepEqual } from ".";
import { showLog } from ".";

export const createClosedArea = (areaList: any[], origin: {x: number, y:number}): THREE.Group | undefined => {

  if(areaList === undefined || areaList.length === 0) {
    return undefined
  }
  const mopMaterial = new THREE.MeshBasicMaterial({
    color: "#ff00ff", //
    opacity: 0.5,
    transparent: true,
  });

  const sweepMaterial = new THREE.MeshBasicMaterial({
    color: 0xff0000, //
    opacity: 0.5,
    transparent: true,
  });

  const zoneMaterial = new THREE.MeshBasicMaterial({
    color: 0x7fffff, //
    opacity: 0.5,
    transparent: true,
  });

  const wallMaterial = new THREE.MeshBasicMaterial({
    color: 0xff0000,
  });

  let areaH = 6
  let areaList_  = areaList.filter(area => area.type !== 1)
  const group = new THREE.Group()
  group.name = ObjectType.area

  areaList_.forEach((area) => {
    let points = area.points.map((pt:any) => { 
      return {x: pt.x + origin.x, y: pt.y + origin.y}
    })

    if (area.type === 4) {
      let wall = createMesh(0.5, areaH, twoPointDistance(points[0],points[1]), wallMaterial, points[0])
      wall.position.set((points[0].x + points[1].x) / 2, -2, (points[0].y + points[1].y) / 2);
      let angle = Math.atan2(points[0].x - points[1].x, points[0].y - points[1].y)
      wall.rotateY(angle)
      group.add(wall)
    } else {
    
      let width = points[1].x - points[0].x
      let depth = points[2].y - points[0].y

      const shape = new THREE.Shape();
      shape.lineTo(width, 0);
      shape.lineTo(width, depth);
      shape.lineTo(0, depth);

      const path3 = new THREE.Path();
      const borderW = 1
      path3.moveTo(borderW, borderW);
      path3.lineTo(width - borderW, borderW);
      path3.lineTo(width - borderW, depth - borderW);
      path3.lineTo(borderW, depth - borderW);
      shape.holes.push(path3);

      const geometry = new THREE.ExtrudeGeometry(shape, {
          depth: areaH,
          bevelEnabled:false,
          curveSegments:50,
      });
      let material = area.type === 2 ? zoneMaterial : area.type === 3 ? mopMaterial : sweepMaterial 
      const cube = new THREE.Mesh(geometry, material);
      cube.rotateX(Math.PI / 2)
      cube.position.set(points[0].x, 1, points[0].y);
      group.add(cube)
    }
  })
  return group
}

export const createWallGeometry = (group: THREE.Group, material: THREE.Material, x: number, y: number, boxProps: boxProps, wallHeight: number, origin?: 
  {x: number, y: number}) => {
    
    if(group.children.length && origin) {
      let userData0 = {relativeX: x - origin.x, relativeY: y - origin.y, props: boxProps, state: 0}
      let resArr = group.children.filter(obj => deepEqual(obj.userData, userData0))
      if(resArr.length > 0){
        let mesh = resArr[0]
        mesh.position.set(x + 0.5, 2.5, y + 0.5);
        mesh.userData.state = 1
        return
      }
    }

    let width = boxProps.direction === 'row' ? boxProps.length : 1;
    let depth = boxProps.direction === 'row' ? 1 : boxProps.length;
    const geometry = new THREE.BoxGeometry(width, wallHeight, depth);
    const cube = new THREE.Mesh(geometry, material);
    cube.position.set(x + 0.5, 2.5, y + 0.5);
    if(origin) {
      cube.userData = {relativeX: x - origin.x, relativeY: y - origin.y, props: boxProps, state: 2}
    }
    group.add(cube);
}

const createMesh = (width: number, height: number, depth: number, material?: THREE.Material, position?: {x: number, y: number}) => {
    const geometry = new THREE.BoxGeometry(width, height, depth);
    const cube = new THREE.Mesh(geometry, material);
    position && cube.position.set(position.x, 0, position.y);
    return cube
}

export const destoryObject = (object: any) => {
    object.material && object.material.dispose();
    object.geometry && object.geometry.dispose();
    object.clear();
    object.parent && object.removeFromParent();
    object = null;
}

export const drawPath = (pathData: { x: number; y: number, type: string}[], origin: {x: number, y: number}, width: number): THREE.Line => {

    let vectorArr = pathData.map(pt => {
      return new THREE.Vector2(pt.x + origin.x, pt.y + origin.y)
    })

    let curve = new THREE.SplineCurve(vectorArr);
    let points = curve.getPoints( vectorArr.length - 1 );
    let geometry = new THREE.BufferGeometry().setFromPoints( points );
    let material = new THREE.LineBasicMaterial( { color : 0xffffff, linewidth: width } );
    let splineObject = new THREE.Line( geometry, material );

    splineObject.rotateX(Math.PI / 2);
    splineObject.position.set(0,-4.9,0);
    splineObject.name = ObjectType.path;

    return splineObject
}

/*  */
export const makeTextSprite = (text: string, parameters?: any): THREE.Sprite => {
    if (parameters === undefined) parameters = {}
    let fontface = parameters.hasOwnProperty("fontface") ?
      parameters["fontface"] : "Arial"
    /*  */
    let fontsize = parameters.hasOwnProperty("fontsize") ?
      parameters["fontsize"] : 16
   
    let color = parameters.hasOwnProperty("color") ?
      parameters["color"]: 'rgba(0, 0, 0, 1.0)'
    /*  */
    let borderThickness = parameters.hasOwnProperty("borderThickness") ?
      parameters["borderThickness"] : 2
    /*  */
    let borderColor = parameters.hasOwnProperty("borderColor") ?
      parameters["borderColor"] : { r: 200, g: 200, b: 200, a: 0.0 }
    /*  */
    let backgroundColor = parameters.hasOwnProperty("backgroundColor") ?
      parameters["backgroundColor"] : { r: 255, g: 255, b: 255, a: 0.0 }
    /*  */
    let canvas = document.createElement('canvas');
    let context = canvas.getContext('2d')

    if (!context) { 
        return new THREE.Sprite() 
    }
    /*  */
    context.font = "Bold " + fontsize + "px " + fontface
    /*  */
    let metrics = context.measureText(text);
    let textWidth = metrics.width
    /*  */
    context.fillStyle = "rgba(" + backgroundColor.r + "," + backgroundColor.g + "," +
      backgroundColor.b + "," + backgroundColor.a + ")"
    /*  */
    context.strokeStyle = "rgba(" + borderColor.r + "," + borderColor.g + "," +
      borderColor.b + "," + borderColor.a + ")";
    context.lineWidth = borderThickness
    /*  */
    roundRect(context, borderThickness / 2, borderThickness / 2, textWidth + borderThickness + 6, fontsize * 1.4 + 6 +
      borderThickness,
      6)
    /*  */
    context.fillStyle = color;
    context.fillText(text, borderThickness + 3, fontsize + 3 + borderThickness)
    /*  */
    let texture = new THREE.Texture(canvas);
    texture.needsUpdate = true
    let spriteMaterial = new THREE.SpriteMaterial({
      map: texture
    });
    let sprite = new THREE.Sprite(spriteMaterial)
    /*  */
    sprite.scale.set(60, 30, 0)
    return sprite
  };
   
  /*  */
  function roundRect(ctx: any, x: number, y: number, w: number, h: number, r: number) {
   
    ctx.beginPath();
    ctx.moveTo(x + r, y);
    ctx.lineTo(x + w - r, y);
    ctx.quadraticCurveTo(x + w, y, x + w, y + r);
    ctx.lineTo(x + w, y + h - r);
    ctx.quadraticCurveTo(x + w, y + h, x + w - r, y + h);
    ctx.lineTo(x + r, y + h);
    ctx.quadraticCurveTo(x, y + h, x, y + h - r);
    ctx.lineTo(x, y + r);
    ctx.quadraticCurveTo(x, y, x + r, y);
    ctx.closePath();
    ctx.fill();
    ctx.stroke();
   
  }