import * as THREE from "three";
import { FloorMaterial } from "../config/interface";
import { getZoneColor} from "./mapUtils";
import { memorize } from ".";

export const getVertexShader = () => {
    return `
    varying vec2 vUv;
    void main() {
      vUv = uv;
      gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
    }
 `;
}

export const getFragmentShader = (type: FloorMaterial): string => {
    if(type === FloorMaterial.zone) {
        return `
        uniform sampler2D bloomTexture;
        uniform vec2 repeatV;
        varying vec2 vUv;
          void main() {
            vec4 color1 = texture2D(bloomTexture,vUv);
            if(color1.a == 0.0){
                 discard;
             }else{
                gl_FragColor = color1;
             }
          }
    `;
    }else {
        return `
        uniform sampler2D bloomTexture;
        uniform sampler2D picTexture;
        uniform vec2 repeatV;
        varying vec2 vUv;
          void main() {
            vec4 color1 = texture2D(bloomTexture,vUv);
            if(color1.a == 0.0){
                 discard;
             }else{
                gl_FragColor = texture2D(picTexture,vec2(fract(vUv.x * repeatV.x), fract(vUv.y * repeatV.y)));
             }
          }
    `;
    }
}

export const getMapTexture = (map: string, mapW: number, mapH: number, zones: number[], zoneColors?: string[]): THREE.DataTexture => {
    let imageData = new Uint8Array(mapW * mapH * 4);
    let colorMap: {[key: string]: number[]} = {};
    for (let i = 0; i < map.length / 2; i++) {

        let ptv = parseInt(map.slice(i * 2, i * 2 + 2), 16)
        let zoneColor;
        if(colorMap[`${ptv}`]) {
          zoneColor = colorMap[`${ptv}`]
        } else {
          zoneColor = getZoneColor(ptv, zones.length > 0, zoneColors)
          colorMap[`${ptv}`] = zoneColor
        }
        let isShow = ptv === 0 || (ptv > 3 && ptv < 36)

        imageData[i * 4] = isShow ? zoneColor[0] : 0;
        imageData[i * 4 + 1] = isShow ? zoneColor[1] : 0;
        imageData[i * 4 + 2] = isShow ? zoneColor[2] : 0;
        imageData[i * 4 + 3] = isShow ? 1 : 0;
    }

    let texture = new THREE.DataTexture(imageData, mapW, mapH, THREE.RGBAFormat);
    texture.needsUpdate = true;
    return texture
}