
import './App.css';
import React, { Component } from 'react';
import ThreeMap from "./ThreeMap";
import { deepEqual, showLog } from "./utils";
import { ImapData } from './config/interface';

interface IState {
  mapData: ImapData | null;
}

export default class MapView extends Component<{ },IState> {

  constructor(props: any) {
    super(props);

    var self = this
    const eventListener = (nativeEvent: any) => {
      if (!nativeEvent.data) {
        return
      }
      let mapInfo = typeof(nativeEvent.data) === 'string' ? JSON.parse(nativeEvent.data) : nativeEvent.data
      if(mapInfo.mapConfig && !mapInfo.map && this.state.mapData){
        let lastState = JSON.parse(JSON.stringify(this.state.mapData));
        lastState.mapConfig = mapInfo.mapConfig;
        this.setState({
          mapData: lastState
        })
      }
      if (!mapInfo || !mapInfo.map  || mapInfo.map.length === 0) {
        return;
      }
      self.setState({
        mapData: {
          width: mapInfo.size.width,
          height: mapInfo.size.height,
          origin: mapInfo.origin,
          map: mapInfo.map,
          mapId: mapInfo.mapId,
          pathData: mapInfo.pathData,
          pilePosition: mapInfo.pilePosition,
          roomInfo: mapInfo.roomInfo,
          areaData: JSON.parse(mapInfo.areaData),
          mapConfig: mapInfo.mapConfig
        }
      })
    }
    //documentioswindow
    window.addEventListener('message', eventListener);
    document.addEventListener('message', eventListener);

    this.state = {
      mapData: null,
    }
  }

  componentDidMount() {
 
  }

  shouldComponentUpdate(nextProps: Readonly<{ }>, nextState: Readonly<IState>, nextContext: any): boolean {
    const { mapData } = this.state
    const { mapData: nextMap} = nextState
    if ((!mapData && nextMap) || (mapData && nextMap && nextMap.map !== mapData.map) || !deepEqual(nextMap?.mapConfig, mapData?.mapConfig)) {
      return true
    } else {
      return false
    }
  }

  render() {
    return (
        <div className="App">
          {/*<header className="App-header">*/}
            {
              this.state.mapData ? <ThreeMap mapData={this.state.mapData}/> : null
            }
          {/*</header>*/}
        </div>
    );
  }
}