import React, {PureComponent} from "react";
import * as THREE from "three";
import {OrbitControls} from "three/examples/jsm/controls/OrbitControls";
// import Stats from 'three/examples/jsm/libs/stats.module';
import {getWallData, boxProps, getChargeDirection} from "./utils/mapUtils";
import {calculateAngle, deepEqual, showLog} from './utils';
import { makeTextSprite, createWallGeometry, createClosedArea, destoryObject, drawPath } from "./utils/objectMaker";
import { getVertexShader, getFragmentShader, getMapTexture } from "./utils/shader";
import { loadRobotObj, loadChargeObj } from "./utils/loader";
import { ImapData, FloorMaterial, ObjectType, IMapConfig } from "./config/interface";

interface IProps {
    mapData: ImapData
}

class ThreeMap extends PureComponent <IProps> {

    mapData: ImapData;
    mapScene: THREE.Scene | null = null;
    camera: THREE.PerspectiveCamera | null = null;
    renderer: THREE.WebGLRenderer | null = null;
    orbitCtrls: OrbitControls | null = null;
    animationId: number = -1;
    floorType: FloorMaterial = FloorMaterial.zone;
    woodTexture: THREE.Texture | undefined;
    tileTexture: THREE.Texture | undefined;
    mapConfig: IMapConfig;
    wallHeight: number;
    wallColor: number = 0xeeeeee

    constructor(props: any) {
        super(props);

        this.mapData = props.mapData;
        this.mapConfig = props.mapData.mapConfig;
        this.wallHeight = this.mapConfig.wallHeight ?? 15
    }
    componentDidMount() {
        if(this.mapData.mapConfig){
            this.floorType = this.mapData.mapConfig.floorType
        }
        this.woodTexture = new THREE.TextureLoader().load('ai_floor5.jpeg')
        this.tileTexture = new THREE.TextureLoader().load('ai_cz_floor2.jpg')
        this.initThree();
    }

    shouldComponentUpdate(nextProps: Readonly<IProps>, nextState: Readonly<{}>, nextContext: any): boolean {
        if (nextProps.mapData.map !== this.props.mapData.map) {
            let mapChg = nextProps.mapData.mapId !== this.props.mapData.mapId
            this.mapData = nextProps.mapData
            this.updateMap(this.mapData, this.mapData.width, this.mapData.height, mapChg)
            return true
        } else if(!deepEqual(nextProps.mapData.mapConfig, this.props.mapData.mapConfig) && nextProps.mapData.mapConfig) {
            this.mapData = nextProps.mapData
            this.updataFloorMaterial(this.mapScene!, nextProps.mapData.mapConfig.floorType)
            changeObject(this.mapScene!, nextProps.mapData.mapConfig, this.props.mapData.mapConfig, getChargeDirection(this.mapData))
            return true
        } else if (!deepEqual(nextProps.mapData.areaData, this.props.mapData.areaData)) {
            updateArea(this.mapScene!, nextProps.mapData, nextProps.mapData.mapConfig, this.props.mapData.mapConfig)
            return true
        }
        return false
    }

    initThree = () => {
        const container = document.getElementById('container')
        if(!container) { return }
        if(container.childNodes.length > 0) {
            for (let i = container.childNodes.length - 1; i > -1 ; i--) {
                container.removeChild(container.childNodes[i])
            }
        }

        const mapW = this.mapData.width;
        const mapH = this.mapData.height;

        this.initScene()
        this.initCamera()
        this.initRender(container)
        this.initCtrls(container)
        this.initLight()
        
        const chargeAngle = getChargeDirection(this.mapData)

        drawMap(this.mapScene!, this.mapData, this.wallHeight, this.mapConfig.wallColor ?? this.wallColor);
        createFloorZone(this.mapScene!, this.mapData, this.floorType, this.getFloorTexture(this.floorType), this.mapConfig.roomColors)
        drawPath_(this.mapScene!, this.mapData, Math.PI-chargeAngle)
        loadChargeObj(this.mapScene!, this.mapData.pilePosition.x + this.mapData.origin.x, this.mapData.pilePosition.y +
            this.mapData.origin.y,this.mapData.mapConfig.pileColor,this.mapData.mapConfig.pileType, chargeAngle)

        if(this.mapConfig.showVirtualArea) {
            let area = createClosedArea(this.mapData.areaData, this.mapData.origin)
            area && this.mapScene!.add(area)
        }

        // const stats = Stats();
        // document.body.appendChild(stats.domElement);

        var self = this
        const animate = function () {
            // stats.update();
            self.renderer!.render(self.mapScene!, self.camera!);
            self.animationId = requestAnimationFrame(animate);     
        };
        animate();

        this.camera!.lookAt(mapW / 2, this.wallHeight, mapH / 2);
    }

    //         
   initScene = () => {
        this.mapScene = new THREE.Scene();
   }       

    //
    initCamera = () => {
        this.camera = new THREE.PerspectiveCamera(90, window.innerWidth / window.innerHeight, 1, 10000);
        this.camera.position.set(this.mapData.width / 2 , 120, this.mapData.height + this.wallHeight);
    }

    //
    initRender = (container: HTMLElement) => {
        this.renderer = new THREE.WebGLRenderer({antialias: true});
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        this.renderer.setClearColor('#f7f7f7', 1.0);
        this.renderer.shadowMap.enabled = true;
        container.appendChild(this.renderer.domElement);
    }

    //
    initCtrls = (container: HTMLElement) => {
        this.orbitCtrls = new OrbitControls(this.camera!, container);
        this.orbitCtrls.target = new THREE.Vector3(this.mapData.width / 2, 0, this.mapData.height / 2);
        this.orbitCtrls.touches = {
          ONE: THREE.TOUCH.PAN,
          TWO: THREE.TOUCH.DOLLY_ROTATE
        };
        this.orbitCtrls.maxPolarAngle = Math.PI/2; //
    }

    //
    initLight = () => {
        const mapW = this.mapData.width;
        const mapH = this.mapData.height;
        let lightH = mapW > mapH ? mapW / 2 : mapH / 2

        const point_light = new THREE.DirectionalLight('white', 1.0);
        point_light.position.set(mapW / 2, lightH > 20 ? lightH: 20, mapH / 2);
        point_light.target.position.set(mapW / 2, 0, mapH / 2)
        this.mapScene!.add(point_light);

        const ambient = new THREE.AmbientLight(0xffffff, 0.5);
        this.mapScene!.add(ambient);
    }

    updateMap = (mapData: ImapData, mapW: number, mapH: number, mapChg: boolean) => {
        if (this.mapScene) {
            const { map, origin, pathData, pilePosition, roomInfo, mapId } = mapData;
            const wallColor = this.mapConfig.wallColor ?? this.wallColor
            this.mapScene.children.forEach((obj: any) => {
                switch (obj.name) {
                    case ObjectType.wall: {
                        if(obj.children && mapChg) {
                            for (let i = obj.children.length - 1; i >= 0; i--) {
                                destoryObject(obj.children[i])
                            }
                            obj.clear();
                        }
                        obj.children.forEach((child: any) => child.userData.state = 0 );

                        const material = new THREE.MeshLambertMaterial({ color: wallColor });
                        let wallArr = getWallData(map, mapW, {x: mapData.pilePosition.x + mapData.origin.x, 
                            y: mapData.pilePosition.y + mapData.origin.y});
                        wallArr.forEach((box) => {
                            createWallGeometry(obj, material, box.x, box.y, box.p, this.wallHeight, origin);
                        })
                        let leftArr = obj.children.filter((child: any) => child.userData.state === 0)
                        obj.remove(...leftArr)
                        obj.needsUpdate = true;
                        for (let i = leftArr.length - 1; i >= 0; i--) {
                            destoryObject(leftArr[i])
                        }

                        break;
                    }
                    case ObjectType.floor:{
                        if(obj.material) {
                            obj.material.uniforms.repeatV.value = {x: mapW / 8, y: mapH / 8};
                            obj.material.uniforms.bloomTexture.value = getMapTexture(map, mapW, mapH,JSON.parse(roomInfo));
                            obj.material.uniforms.picTexture.value = this.getFloorTexture(this.floorType)
                            obj.geometry && obj.geometry.dispose()
                            obj.geometry = new THREE.PlaneGeometry(mapW, mapH);
                            obj.position.set(mapW / 2,-5,mapH / 2);
                            obj.needsUpdate = true;
                        }
                        break;
                    }
                    case ObjectType.path:{
                        if(obj.geometry && pathData.length > 0) {
                            let ptArr: number[] = [];
                            pathData.forEach(pt => {
                                ptArr.push(pt.x + origin.x);
                                ptArr.push(pt.y + origin.y);
                            })
                            obj.geometry.attributes.position = new THREE.BufferAttribute(new Float32Array(ptArr), 2);
                            obj.needsUpdate = true;
                        }
                        break;
                    }
                    case ObjectType.robot:{
                        if(pathData.length > 0) {
                            let robLoc = pathData[pathData.length -1];
                            obj.position.set(robLoc.x + origin.x, -5, robLoc.y + origin.y);
                            obj.rotateY(getRobotAngle(pathData,pilePosition));
                            obj.needsUpdate = true;
                        }
                        break;
                    }
                    case ObjectType.charge:{
                        obj.position.set(pilePosition.x + origin.x, -5, pilePosition.y + origin.y);
                        obj.needsUpdate = true;
                        break;
                    }
                }
            })
        }
    }

    componentWillUnmount() {

        this.animationId && cancelAnimationFrame(this.animationId);
        this.renderer && this.renderer.dispose();
        this.renderer = null;
        this.camera = null;
        this.mapScene && this.mapScene.clear();
        this.mapScene = null;
        this.orbitCtrls = null;
        this.woodTexture && this.woodTexture.dispose();
        this.tileTexture && this.tileTexture.dispose();
    }

    updataFloorMaterial = (scene: THREE.Scene, material: FloorMaterial) => {
        let obj: any = scene.getObjectByName(ObjectType.floor)
        if(obj && obj.material) {
            this.floorType = material;
            obj.material.uniforms.picTexture.value = this.getFloorTexture(material);
            obj.material.fragmentShader = getFragmentShader(material);
            obj.material.needsUpdate = true;
            obj.needsUpdate = true;
        }
    }

    getFloorTexture = (type: FloorMaterial): THREE.Texture | undefined => {

        if(type === FloorMaterial.wood) {
            return this.woodTexture ? this.woodTexture : new THREE.TextureLoader().load('ai_floor5.jpeg')
        }else if(type === FloorMaterial.tile) {
            return this.tileTexture ? this.tileTexture : new THREE.TextureLoader().load('ai_cz_floor2.jpg')
        } else {
            return undefined
        }
    }

    render() {
        return (
            <div id='container'></div>
        );
    }
}

//
function drawMap (scene: THREE.Scene, mapData: ImapData, wallHeight: number, color: THREE.ColorRepresentation) {

    const material = new THREE.MeshLambertMaterial({
        color: color,
    });

    const group = new THREE.Group()
    group.name = ObjectType.wall

    let wallArr = getWallData(mapData.map, mapData.width, {x: mapData.pilePosition.x+mapData.origin.x, 
        y: mapData.pilePosition.y+mapData.origin.y})
    wallArr.forEach((box, index) => {
        createWallGeometry(group, material, box.x, box.y, box.p, wallHeight, mapData.origin)
    })
    scene.add(group)
}

function drawPath_ (scene: THREE.Scene, mapData: ImapData, angle: number, pathW: number = 2) {

    let pathData = mapData.pathData
    if(!pathData || pathData.length === 0) {
        return
    }

    let transitionArr: number[] = []
    let pathSection = []
    pathData.forEach((path,index) => {
        if(path.type === 'transitions') { transitionArr.push(index) }
    })
    for (let i = 0; i < transitionArr.length; i++) {
        if(i === 0) {
            pathSection.push(pathData.slice(0, transitionArr[i]))
        }
        if(i < transitionArr.length - 1) {
            pathSection.push(pathData.slice(transitionArr[i], transitionArr[i+1]))
        }
        if(i === transitionArr.length - 1) {
            pathSection.push(pathData.slice(transitionArr[i], pathData.length))
        }
    }
    transitionArr.length === 0 && pathSection.push(pathData)

    let origin = mapData.origin
    let robotColor = mapData.mapConfig.robotColor

    pathSection.forEach(pathArr => {
        let splineObject = drawPath(pathArr, origin, pathW)
        scene.add(splineObject);
    })

    loadRobotObj(scene,pathData[pathData.length - 1].x + origin.x,pathData[pathData.length - 1].y + origin.y, 
        getRobotAngle(pathData,mapData.pilePosition,angle), robotColor)
}

//
function createFloorZone(scene: THREE.Scene, mapData: ImapData, type: FloorMaterial, floorTexture?: THREE.Texture, zoneColors?: string[]) {

    let mapW = mapData.width
    let mapH = mapData.height
    let zones = mapData.roomInfo ? JSON.parse(mapData.roomInfo) : []

    let texture = getMapTexture(mapData.map,mapW,mapH,zones,zoneColors)
    const vertex = getVertexShader();

    const shaderMaterial = new THREE.ShaderMaterial({
        vertexShader: vertex,
        fragmentShader: getFragmentShader(type),
        uniforms: {
            repeatV: {value : {x: mapW / 8, y: mapH / 8}},
            bloomTexture: { value: texture },
            picTexture: {value: floorTexture}
        },
        side: THREE.BackSide
    });
    let planeGeometry = new THREE.PlaneGeometry(mapW, mapH);
    const floor = new THREE.Mesh(planeGeometry, shaderMaterial);
    floor.name = ObjectType.floor;
    floor.rotateX(Math.PI * 0.5);
    floor.position.set(mapW / 2,-5,mapH / 2);

    if(floorTexture) {
        setTimeout(() => { scene.add(floor) }, 50);
    } else {
        scene.add(floor)
    }
}

//
function changeObject(scene: THREE.Scene, config: any, lastConfig: any, rotateY: number) {
    let chargeObj: any = scene.getObjectByName(ObjectType.charge)
    
    if(chargeObj) {
        if(lastConfig && config && lastConfig.pileType !== config.pileType) {
            let curPosition = chargeObj.position

            chargeObj.traverse((child: any) => {
                destoryObject(child)
            });
            chargeObj.clear();
            chargeObj.removeFromParent();
            chargeObj = null;
            loadChargeObj(scene,curPosition.x,curPosition.z - 1,config.pileColor,config.pileType, rotateY)
        } else {
            chargeObj.children.forEach((child: any) => {
                child.material && child.material.color.set(config.pileColor);
                child.material.needsUpdate = true
            })
        }
    }
    let robotObj: any = scene.getObjectByName(ObjectType.robot)
    if(robotObj) {
        robotObj.children.forEach((child: any) => {
            child.material && child.material.color.set(config.robotColor);
            child.material.needsUpdate = true
        })
    }
}

//
function updateArea(scene: THREE.Scene, mapData: ImapData, config: any, lastConfig: any) {
    let areaObj: any = scene.getObjectByName(ObjectType.area)
    if(areaObj.children) {
        for (let i = areaObj.children.length - 1; i >= 0; i--) {
            let child1 = areaObj.children[i];
            destoryObject(child1)
        }
        areaObj.clear();
        areaObj.removeFromParent();
        areaObj = null;
    }
    let area = createClosedArea(mapData.areaData, mapData.origin)
    area && scene.add(area)
}

function getRobotAngle(points: { x: number; y: number, type: string }[], pilePosition:{x: number; y: number}, angle: number = 0): number {

    if(points.length > 2) {
        let pt0 = points[points.length - 3];
        let pt1 = points[points.length - 2];

        if (points.length > 5) {
            let lastPt = points[points.length - 1];
            let dis = Math.sqrt(Math.pow(lastPt.y-pilePosition.y,2)+Math.pow(lastPt.x-pilePosition.x,2))
            if (dis < 3.0) { return  angle }
        }
        return  calculateAngle(pt0, pt1)
    }
    return  angle
}

export default ThreeMap