import {OBJLoader} from  'three/examples/jsm/loaders/OBJLoader'
import { ObjectType } from '../config/interface';
import { showLog } from '.';

export const loadRobotObj = (scene: THREE.Scene, x: number, y: number, angle: number, color?: string) => {
    new OBJLoader().load("livi_robot.obj", function (loadMash) {
        loadMash.position.set(x, -5, y);
        loadMash.scale.set(0.5,0.5,0.5)
        loadMash.name = ObjectType.robot;
        loadMash.rotateY(angle);
        if(color){
            loadMash.children.forEach((child: any) => {
                child.material && child.material.color.set(color);
                // child.material.needsUpdate = true
            })
        }
        scene.add(loadMash);
    });
}

export const loadChargeObj = (scene: THREE.Scene, x: number, y: number, color?: string, pileType?: string, angel?: number) => {

    let objStr = pileType === 'bucket' ? 'rock_dust.obj': 'rock_chargebase.obj'
    new OBJLoader().load(objStr, function (loadMash) {
        loadMash.position.set(x, -5, y + 1);
        loadMash.name = ObjectType.charge;
        loadMash.rotateY(angel ? angel : 0);
        if(color){
            loadMash.children.forEach((child: any) => {
                child.material && child.material.color.set(color);
                // child.material.needsUpdate = true
            })
        }
        scene.add(loadMash);
    });
}